import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutUsComponent } from './about-us/about-us.component';
import { AerodromeComponent } from './aerodrome/aerodrome.component';
import { OurteamComponent } from './ourteam/ourteam.component';

const routes: Routes = [
  {path: '', redirectTo: 'info', pathMatch: 'full' },
  {path:'info', component: AboutUsComponent},
  {path:'aerodrome', component: AerodromeComponent},
  {path:'forth', component: OurteamComponent},
  {path:'footer', component: OurteamComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
